.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f7f7f7;
}

.login-form-wrapper {
  width: 100%;
  max-width: 400px;
  padding: 40px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.login-title {
  font-size: 28px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 10px;
  color: #333;
}

.login-subtitle {
  font-size: 18px;
  text-align: center;
  margin-bottom: 30px;
  color: #666;
}

.login-form {
  display: flex;
  flex-direction: column;
}

.login-field {
  margin-bottom: 20px;
}

.login-field label {
  display: block;
  margin-bottom: 5px;
  font-weight: 500;
  color: #555;
}

.login-field input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
}

.login-field input:focus {
  border-color: #666;
  outline: none;
}

.login-button {
  padding: 12px;
  background-color: #4285f4;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
}

.login-button:hover {
  background-color: #3367d6;
}

.login-button:disabled {
  background-color: #b3c6e6;
  cursor: not-allowed;
}

.login-error {
  color: #d93025;
  margin-bottom: 20px;
  text-align: center;
  font-size: 14px;
  background-color: #fce8e6;
  padding: 10px;
  border-radius: 4px;
}